(function() {
  'use strict';

  angular
    .module('source')
    .controller('MainController', MainController)
    .animation('.fade', fade)
    .animation('.slide-animation', slideAnimation);


    function fade() {
      return {
        enter: function(element, done) {
          TweenMax.fromTo(element, 0.25, {opacity: 0}, {opacity:1, onComplete: done});
        },
        leave: function(element, done) {
          TweenMax.fromTo(element, 0.25, {opacity: 1}, {opacity: 0, onComplete: done});
        }
      };
    }

    function slideAnimation($media) {
      return {
        addClass: function(element, className, done) {
          if (className === 'wide-screen') {
            TweenMax.fromTo(element, 0.5, {maxWidth: '400px'}, {maxWidth: '877px', onComplete:done});
          }
        },
        removeClass: function(element, className, done) {
          var wide = $media.raw('(min-width: 1000px)');

          if (className === 'wide-screen') {
            var delay = wide ? 1:0;
            TweenMax.fromTo(element, 0.5, {maxWidth: '877px'}, {delay: delay, maxWidth: '400px', onComplete:done});
          }
        }
      };
    }

  /** @ngInject */
  function MainController($state, $timeout, $interval, $scope, Restangular) {
    var vm = this;
    var scriptRunning;

    vm.state = $state;

    var hasVisited = localStorage.getItem('hasVisited');
    if (!hasVisited) localStorage.setItem('hasVisited', true);

    var firstSessionLoad = !sessionStorage.getItem('hasVisited');
    if (firstSessionLoad) sessionStorage.setItem('hasVisited', true);

    var introFirstVisit = [
      {message: 'Welcome!', delay: 5},
      {message: 'Unfortunately, Nick is not here at the moment, so I\'m here on his behalf.  Allow me to serve you.', delay: 6},
      {message: 'Ask me a question below to learn more.', delay: 4}
    ];

    var introHasVisited = [
      {message: 'Welcome back!', delay: 5},
      {message: 'Feel free to ask me a question.', delay: 4}
    ];

    function playScript(script, index, count) {
      if (angular.isArray(script)) {
        index = index || 0;
        scriptRunning = $timeout(function() {
          vm.chatText = script[index].message;
          if (index !== script.length - 1 || count === 1) {
            playScript(script, index +1);
          } else {
            scriptRunning = $timeout(destroyScript, script[index].delay * 1000);
          }
        }, script[index-1] ? script[index-1].delay * 1000 : 0);
      } else {
        vm.chatText = script;
      }
    }

    function destroyScript() {
      $timeout.cancel(scriptRunning);
      scriptRunning = null;
    }

    function chooseRandom() {
      if (!scriptRunning && !document.hidden) {
        Restangular.oneUrl('jokes', 'http://mentalfloss.com/api/1.0/views/amazing_facts.json?limit=1&display_id=xhr&cb=' + Math.random()).get()
        .then(function(data) {
          if (data[0].fact_body.length < 200) {
            playScript(decodeURIComponent(data[0].fact_body.replace('%', '%25')));
          }
        });
      }
    }

    vm.playScene = function() {
      if (!hasVisited) {
        playScript(intro);
      } else {
        if (firstSessionLoad) {
          playScript(introHasVisited);
        } else {
          chooseRandom();
        }
      }

      $interval(chooseRandom, (Math.floor(Math.random() * (40 - 20 + 1)) + 20) * 1000);
    };


    $scope.$on('$destroy', function(){
      $timeout.cancel(scriptRunning);
      $interval.cancel(chooseRandom);
    });
  }
})();

(function() {
  'use strict';

  angular
    .module('source')
    .directive('robot', robot);

  /** @ngInject */
  function robot($timeout) {
    return {
      restrict: 'E',
      scope: {
        chatText: '=text',
        onLoad: '&'
      },
      templateUrl: 'app/components/robot.html',
      link: function(scope, el, attr) {
        TweenLite.defaultEase = Power0.easeNone;
        /////////////Elements Query Code
        //BY Tag Name Img
        var imgs = document.getElementsByTagName("img");

        var robot = {
          bulb: imgs.bulb,
          lightGlare: imgs.glare,
          head: imgs.head,
          armLeftBicep: imgs.armleftbicep,
          armLeftFront: imgs.armleftfront,
          armRightBicep: imgs.armrightbicep,
          armRightFront: imgs.armrightfront,
          torsoTop: imgs.torsotop,
          torsoDown: imgs.torsodown,
          eyelids: imgs.eyelids,

          monitor: imgs.monitor,
          can: imgs.can,
          candrink: imgs.candrink,
          antennaleft: imgs.antennaleft,
          antennaright: imgs.antennaright,

         //By Div ID
          armRight: document.getElementById("armright"),
          armLeft: document.getElementById("armleft"),
          light: document.getElementById("light")
        };

        // Animation Data
        var typing = new TimelineMax({repeat: -1, yoyo: true, paused: true});
        typing.to(robot.armLeftFront, 0.05, {rotation: 15})
              .to(robot.armRightFront, 0.05, {rotation: -15});

        var drink = new TimelineMax({repeatDelay: 1, repeat: 1, yoyo:true, paused:true});
        drink.to(robot.armRight, 0.2, {rotation: 32}, "start")
             .to(robot.armRightFront, 0.2, {rotation: -45}, "start")
             .to(robot.armRightFront, 0.2, {rotation: "+=65"}, "lift")
             .to([robot.can, robot.candrink], 0.2, {y: "-=60", x: "+=8", rotation: "+=35"}, "lift")
             .to(robot.armRight, 0.2, {rotation: "+=42,", scaleY: "-=0.2"}, "liftmore")
             .to(robot.candrink, 0.2, { opacity: 1}, "liftmore")
             .to([robot.can, robot.candrink], 0.2, {x: "+=10", y: "-=60", rotation: "+=20"}, "liftmore");

        var antennaflutter = new TimelineMax({repeat: 3, yoyo: true, paused: true});
        antennaflutter.to(robot.antennaleft, 0.05, {rotation: 15})
                      .to(robot.antennaright, 0.05, {rotation: -15});

        function raiseHead() {
          robot.head.setAttribute('src','./assets/images/robot/robot-head-up.png');
          robot.eyelids.setAttribute('src','./assets/images/robot/eyelidsup.png');
        }

        function lowerHead() {
          robot.head.setAttribute('src','./assets/images/robot/robot-head-down.png');
          robot.eyelids.setAttribute('src','./assets/images/robot/eyelidsdown.png');
        }

        function say(wpm, delay) {
          $timeout(function () {
            scope.showChat = false;
            var what = scope.chatText;
            var numCharacters = what.split('').length;
            wpm = wpm || 0;
            delay = delay || 0;

            // BEGIN
            typing.play();
            lowerHead();
            simulateTyping(numCharacters);

            function simulateTyping(count) {
              $timeout(function () {
                typing.pause();
                raiseHead();
                scope.chatTextTyped = scope.chatText;
                scope.showChat = true;
              }, (numCharacters / (wpm * 5)) * 60 * 1000 );
            }
          }, delay);
        }

        ///////////Timeline Code
        function intro() {
          TweenMax.to(robot.light, 3, { rotation: "-3", yoyo: true, repeat: -1, ease: Power1.easeInOut });

          TweenMax.to([robot.torsoTop, robot.torsoDown, robot.head, robot.armLeft, robot.armRight, robot.eyelids, robot.antennaright, robot.antennaleft], 2, { marginTop: 5, yoyo: true, repeat: -1, ease: Power1.easeInOut });
          TweenMax.to(robot.lightGlare, 0.2, { opacity: 0.95, yoyo: true, repeat: -1, ease: Bounce.easeInOut });
          TweenMax.to(robot.monitor, 0.2, { opacity: 0.95, yoyo: true, repeat: -1, ease: Bounce.easeInOut });
          TweenMax.from(robot.eyelids, 0.25, { delay: 1, immediateRender: false, opacity: 1, repeat: -1, repeatDelay: 5});
        }

        var coffeeBreak = new TimelineMax({repeat: -1});
        coffeeBreak.add(drink.play(), 15)
                   .add(antennaflutter.play(), 6);

        $(imgs[imgs.length - 1]).load(function() {
            scope.imagesLoaded = true;
            intro();
            scope.onLoad();
        });

        scope.$watch('chatText', function(newValue) {
          if (newValue)
            say(900);
        }, true);
      }
    };
  }
})();

